import React, { useState } from 'react';
import {
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators,
  CarouselCaption,
} from 'reactstrap';
import '../assets/testimonial.css';

const items = [
  {
    src:
      'https://images.pexels.com/photos/316466/pexels-photo-316466.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=500',
    altText: 'Slide 1',
    caption:
      'Terapia obejmowaa cay ukad odpornociowy, ze szczeglnym uwzgldnieniem czci ldwiowej krgosupa, ktry bola mnie od jakiego czasu. Terapia trwaa 4 dni. Po zakoczeniu cyklu terapeutycznego, w cigu kilku kolejnych dni ustpi bl krgosupa. Naley zaznaczy, e w tym czasie nie zaywaam lekw, ani te nie korzystaam z klasycznej fizykoterapii. Od tego czasu mino kilka miesicy - do dzisiaj nie odczuwam adnych dolegliwoci z tego tytuu.',
    tags: '#terapia na odlego',
  },
  {
    src:
      'https://images.pexels.com/photos/316466/pexels-photo-316466.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=500',
    altText: 'Slide 2',
    caption:
      'Terapia miaa by robiona z myl o boreliozie. Bl nie do wytrzymania, rce rednio sprawne.Po zakoczeniu 4dniowej terapii przez ponad 2 miesice nie miaam napadu blu w stawach. Efekt dla mnie poraajcy. Z caego serca polecam, szczeglnie ze wzgldu na to, e terapeuci tej metody to ludzie niezwykle dobrzy, uwani, sympatyczni, nie zniszczeni rutyn i przede wszystkim czuli na potrzeb drugiego czowieka.',
    tags: '#borelioza',
  },
  {
    src:
      'https://images.pexels.com/photos/316466/pexels-photo-316466.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=500',
    altText: 'Slide 3',
    caption:
      'Ja, 2 latka. Czsto choruje (proste infekcje garda, nieyt z nosa, kaszel), trwa to zazwyczaj do dugo-kaszel utrzymuje si nawet do 2 miesicy.(...) Przez ok miesic od terapii Jakowi nie przydarzyo si nic prcz pozostaoci kaszelkw w cigu dnia.Szczeglnie przy dzieciach wane jest dla mnie szukanie alternatywnych rozwiza niekonwencjonalnych. Dlatego z caego serca polecam terapi metod Zdenka Domancica.',
    tags: '#terapia dziecka',
  },
];

const Testimonial = props => {
  const [activeIndex, setActiveIndex] = useState(0);
  const [animating, setAnimating] = useState(false);

  const next = () => {
    if (animating) return;
    const nextIndex = activeIndex === items.length - 1 ? 0 : activeIndex + 1;
    setActiveIndex(nextIndex);
  };

  const previous = () => {
    if (animating) return;
    const nextIndex = activeIndex === 0 ? items.length - 1 : activeIndex - 1;
    setActiveIndex(nextIndex);
  };

  const goToIndex = newIndex => {
    if (animating) return;
    setActiveIndex(newIndex);
  };
  const slides = items.map(item => {
    return (
      <CarouselItem
        onExiting={() => setAnimating(true)}
        onExited={() => setAnimating(false)}
        key={item.src}
      >
        <img src={item.src} alt={item.altText} />
        <CarouselCaption
          className="d-block"
          captionText={item.caption}
          captionHeader={item.tags}
        />
      </CarouselItem>
    );
  });

  return (
    <div className="carousel-container d-none d-sm-block">
      <h2 className="text-center">Referencje</h2>
      <Carousel activeIndex={activeIndex} next={next} previous={previous}>
        <CarouselIndicators
          items={items}
          activeIndex={activeIndex}
          onClickHandler={goToIndex}
        />
        {slides}
        <CarouselControl
          direction="prev"
          directionText="Previous"
          onClickHandler={previous}
        />
        <CarouselControl
          direction="next"
          directionText="Next"
          onClickHandler={next}
        />
      </Carousel>
    </div>
  );
};

export default Testimonial;
