import React from 'react';
import Layout from '../components/Layout';
import Header from '../components/Header';
import Scroll from '../components/Scroll';
import Footer from '../components/Footer';
import Social from '../components/Social';
import Testimonial from '../components/Testimonial';
import Certificate from '../components/Certificate';
import demo1 from '../assets/images/demo-screen.jpg';
import CalendlyEmbed from '../components/CalendlyEmbed';
import MoreAboutMe from '../components/MoreAboutMe';
import DiseaseList from '../components/DiseaseList';

const IndexPage = () => (
  <Layout>
    <Header />
    <header className="masthead">
      <div className="container h-100">
        <div className="header-content mx-auto text-justify">
          <div className="button">
            <Scroll type="id" element="features">
              <a href="#features" className="btn btn-outline btn-xl">
                Dowiedz si wicej
              </a>
            </Scroll>
          </div>
        </div>
      </div>
      <div className="overlay"></div>
    </header>

    <section className="download bg-primary text-center" id="aboutMe">
      <div className="container">
        <div className="row">
          <div className="col-md-12 mx-auto">
            <h2 className="section-heading">O mnie</h2>
            <p className="text-justify">
              Ukoczyam studia pedagogiczne na kierunku Pedagogika
              Opiekuczo-Socjalna. Przez wiele lat pracowalam jako instruktor
              jazdy konnej i hipoterapeuta. Moja przygoda z bioterapi
              rozpocza si od potrzeby niesienia pomocy najbliszym. Jednak
              szybko zrozumiaam, e to moja droga yciowa (ju w pierwszym dniu
              nauki wiedziaam, e bd prowadzi terapie grupowe). Ukoczyam
              kursy 1 i 2 stopnia Metody Zdenko Domancica. Posiadam tytu
              czeladniczy z zakresu Bioenergoterapii i Naturalnych Metod
              Uzdrawiania, uprawniajcy do wykonywania zawodu na terenie caej
              UE. Z ogromn przyjemnoci pomagam przy kursach dla kolejnych
              adeptw uczcych si Metody Zdenko Domancica. Wspaniale jest
              obserwowa rado i dobre samopoczucie podopiecznych, a potem
              niejednokrotnie widzie ich uczcych si pracy z energi.
            </p>
            <p className="text-justify">
              Na co dzie pracuj w kontakcie bezporednim grupowo i
              indywidualnie. Mog take zaproponowa terapi, ktra nie zna
              granic, czyli bioenergoterapi na odlego. Z mojej pomocy
              korzystaj ludzie, zwierzta i roliny. Za mn ju wiele terapii,
              a ja nadal nie mog wyj z podziwu nad prostot i skutecznoci
              tej metody. Ciesz si, e dobro idzie dalej w wiat.
            </p>
            <p className="text-justify">
              Zajmuj si take odczytami z Kronik Akaszy. Ukoczyam 1 i 2
              stopie tej metody u Gabrielle Orr.
            </p>
          </div>
        </div>
      </div>
    </section>

    <section className="features" id="features">
      <div className="container">
        <div className="section-heading text-justify">
          <h2 className="text-center">Bioenergoterapia</h2>
          <p className="text-muted text-justify">
            Bioterapia ma na celu wyrwnanie stanu energetycznego caej istoty
            jak jestemy (doprowadzenie do rwnowagi fizycznej, emocjonalnej,
            mentalnej i duchowej). Dzieje si to za pomoc energii, ktra niesie
            ze sob doskona informacj. Dysfunkcje pojawiaj si najpierw w
            ciaach energetycznych (w aurze). Kiedy konflikt nie jest rozwizany
            zaczynaj si manifestowa w ciele fizycznym (jakim organie lub
            obszarze). Rwnowaga powoduje swobodny przepyw energii, pobudzenie
            systemu odpornoci, a to prowadzi do regeneracji i samouzdrowienia.
            Uzdrowienie lub zanikanie blu jest spowodowane tym, e dotknite
            obszary ciaa powracaj do rwnowagi. Jeeli ukad odpornociowy
            jest silny, wtedy organizm moe wytworzy kad substancj jaka jest
            mu potrzebna i zwalczy chorob.W ten oto pikny sposb ludzkie
            ciao leczy si samo. Czysta, poinformowana energia kry po ciele i
            dokonuje naprawy jeszcze w cigu 2-3 tygodni od zakoczenia
            zabiegw.
          </p>
          <hr />
          <h2 className="text-center">Bioenergia</h2>
          <p className="text-muted text-justify">
            Bioenergia jest podstaw ycia- bios z greckiego ycie. Inne
            nazwy siy yciowej to Chi, Prana, Ki, Mana, sia witalna, energia
            kosmiczna. Kada istota jest stworzeniem zarwno materialnym jak i
            energetycznym, a do ycia potrzebuje Prany. Dopki kry ona
            swobodnie i ma jej w sobie wystarczajco duo, zostaje zachowana
            rwnowaga i dobrostan. Kiedy energii ycia brakuje lub nie jest ona
            rwnomiernie rozprowadzana  istota zaczyna chorowa. Bioenergia
            zawiera doskona wiedz na temat zdrowia i ycia oraz inne
            zaawansowane informacje. Zawsze dociera do przyczyny problemu z
            jakim boryka si dana jednostka.
          </p>
          <hr />
        </div>
      </div>
    </section>

    <section className="download bg-primary text-center" id="aboutMethod">
      <div className="container">
        <div className="row">
          <div className="col-md-12 mx-auto">
            <h2 className="section-heading">O metodzie</h2>
            <p className="text-justify">
              Zdenko Domancic podczas ponad trzydziestoletniej praktyki uleczy
              prawie milion osb. Bioenergoterapia Metod Zdenko Domancica, to
              najlepiej przebadana naukowo terapia energetyczna. Pragnc naukowo
              zweryfikowa swoj metod, Domancic zwrci si nie tylko do
              lekarzy, ale take do chemikw, fizykw, cybernetykw i
              psychologw. W 1984 roku metod wzili pod lup specjalici z
              midzynarodowej komisji lekarskiej. Najbardziej spektakularn
              czci tego badania byo uzdrowienie 300 osb z gangren such
              (rzekom) i mokr. adna z nich nie miaa amputowanej koczyny.
              Ten ogromny sukces spowodowa, e do kliniki w Kraskiej Grze
              (ktra jest dotowana przez wadze Sowenii i patronuje jej
              Ministerstwo Zdrowia) przybywaj ludzie z caego wiata. Cze z
              nich jest pacjentami, a cz szkoli si w pracy z energi.
            </p>
            <p className="text-justify">
              Inne osignicia Zdenko Domancica, to wspaniae efekty
              terapeutyczne w takich chorobach jak: cukrzyca, Parkinson,
              Alzheimer, astma, artretyzm, taczka typu C, nowotwory, udary i
              wiele innych. Otrzyma prestiow nagrod Prezydenta Sowenii za
              wyniki pracy. Uczestniczy w badaniach nad AIDS prowadzonych przez
              Uniwersytet Stanforda oraz w badaniach nad rakiem w Instytucie
              Onkologii w Lubljanie. W jego klinice leczy si wielu sawnych
              ludzi, w tym soweska reprezentacja sportowcw olimpijskich.
              Wielu jego terapeutw ma wspaniae wyniki w pracy z klientami.
              Osoby, ktre uczestnicz w terapiach bardzo chwal t metod.
              Trafia do ich serc o wiele wczeniej, ni zostaa sprawdzona
              przez ekspertw medycyny akademickiej.
            </p>
            <p className="text-justify">
              Ujmujca jest prostota Metody Zdenko Domancica. Wyszkolony i
              dowiadczony bioterapeuta pracuje ze zdrowiem. Powrt do zdrowia
              nie powoduje efektw ubocznych. Jest to metoda bezpieczna i
              komplementarna. Terapeuta Metody Domancica jest narzdziem
              przekazujcym bioenergi pobieran z otoczenia. Dziki temu nie
              traci swojej energii yciowej.
            </p>
            <p className="text-justify">
              Oryginalno Metody Zdenko Domancica kryje si w sposobie
              pobierania i przenoszenia bioenergii na ciao osoby potrzebujcej
              i jej rwnomiernym rozmieszczeniu w organizmie. Technika ta
              zostaa przystosowana do cywilizacji zachodniej: sposobu
              rozumowania, yciowych pogldw, stylu ycia, kultury.
            </p>
            <p className="text-justify">
              Moe by uzupenieniem innych terapii. Jest naturalna, bezbolesna
              i nieinwazyjna. Skutecznie poprawia stan fizyczny i silnie
              oddziauje na psychik wzmacniajc j. Nie ma ona nic wsplnego z
              jakkolwiek religi.
            </p>
          </div>
        </div>
      </div>
    </section>

    <section className="text-center" id="aboutTherapy">
      <div className="container">
        <div className="row">
          <div className="col-md-12 mx-auto">
            <h2 className="section-heading">O terapii</h2>

            <div className="row">
              <div className="col-lg-12 my-auto">
                <div className="container-fluid">
                  <div className="row">
                    <div className="col-lg-6">
                      <h3>Terapia grupowa</h3>
                      <p className="text-justify text-muted">
                        Podczas terapii grupowych wsppracuj z innymi
                        terapeutami (2 terapeutw na jednego uczestnika
                        terapii). S one zorganizowane w indywidualnym podejciu
                        do kadego uczestnika. W zalenoci od wielkoci grupy
                        terapia taka trwa 1- 2 godzin. Zalecane jest przebywanie
                        w sali jak najdusz ilo czasu przed i po
                        indywidualnym zabiegu. Energia dziaa ju w czasie
                        przebywania w pomieszczeniu, niezalenie od
                        indywidualnego zabiegu. Nie ma z gry okrelonej
                        kolejnoci, terapeuci prosz kadego intuicyjnie. Cay
                        zabieg dla jednej osoby trwa 15- 20 minut. Cay cykl
                        terapii trwa 4 dni z rzdu. Terapeuta czy si z
                        energi wszechwiata uruchamiajc jej przekaz poprzez
                        odpowiedni sekwencj ruchow rk, a uczestnik terapii
                        przyjmujc j inicjuje proces zdrowienia. Celem terapii
                        jest cakowite wyeliminowanie, zmniejszenie lub
                        zahamowanie problemw zdrowotnych.
                      </p>
                    </div>

                    <div className="col-lg-6">
                      <h3>Terapia indywidualna</h3>
                      <p className="text-justify text-muted">
                        Kady z uczestnikw bioterapii jest zobowizany do
                        kontaktu z lekarzem konwencjonalnym i stosowania si do
                        jego zalece. Nie powinien take bez konsultacji z
                        lekarzem przerywa przyjmowania lekw lub zmniejszania
                        ich dawek. Uczestnicy bioterapii s zobowizani do
                        zrobienia bada lekarskich i wczeniejszego zgoszenia
                        diagnozy oraz objaww choroby (za pomoc formularza
                        kontaktowego). Bioterapeuci Metody Zdenko Domancica nie
                        zalecaj adnych rodkw farmakologicznych i nie
                        udzielaj porad medycznych, nie sugeruj take innych
                        metod komplementarnych, naturalnych itp. Metoda
                        uzdrawiania bioenergi, opracowana przez Zdenko
                        Domancica, nie stanowi zamiennika dla medycyny
                        konwencjonalnej, lecz jest jej uzupenieniem. Medycyna
                        oficjalna jest partnerem dla uzdrawiania bioenergi, a
                        nie konkurencj. Bioenergoterapia jest wietnym
                        uzupenieniem dla medycyny akademickiej.
                      </p>
                    </div>
                    <div className="col-lg-6">
                      <h3>Uwagi, zalecenia</h3>
                      <p className="text-justify text-muted">
                        Zabieg terapii indywidualnej trwa ok. 20-40 minut w
                        zalenoci od schorzenia i jest powtarzany przez 4 dni z
                        zrzdu. W czasie cyklu zabiegw nie powinno si
                        korzysta z innych terapii energetycznych, np.
                        akupunktura, terapia dwikiem, masae itp.
                      </p>
                    </div>

                    <div className="col-lg-6">
                      <h3>Podzikowanie dla terapeuty</h3>
                      <p className="text-justify text-muted">
                        Podzikowanie dla terapeuty jest w ostatnim dniu terapii
                        w formie donacji od serca (w nieoznakowanych kopertach).
                        Uczestnik sam decyduje o wysokoci wpaty zgodnie ze
                        swoim samopoczuciem, moliwociami finansowymi i
                        sumieniem.
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="container text-center">
        <DiseaseList />
      </div>
    </section>

    <section className="testimonial" id="testimonial">
      <div className="container text-center">
        <Testimonial />
      </div>
    </section>

    <section className="contact bg-primary" id="contact">
      <div className="container">
        <Social />
      </div>
    </section>

    <section className="certificate" id="certificate">
      <div className="container text-center">
        <h2>Certyfikaty i szkolenia</h2>
        <Certificate />
      </div>
    </section>

    <Footer />
  </Layout>
);

export default IndexPage;

